html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

img {
  max-width: 100%;
}

body {
  font-family: var(--font-body);
  color: var(--black);
  line-height: 1.4;
  background-color: var(--white);
  margin: 0;
  padding: 0;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin-top: 0;
  font-family: var(--font-header);
  line-height: 1.1;
}

a,
a:visited,
a:active,
a:hover {
  text-decoration: none;
}

a[aria-current=true] {
  font-weight: 700;
  text-decoration: underline;
}

:root {
  --darkGreen: ;
  --nwcGreen: hsl(166, 100%, 20%);
  --lightGreen: hsl(109, 61%, 43%);
  --northernBlue: hsl(208, 100%, 33.5%);
  --lightBlue: hsl(214, 58%, 55%);
  --orange: hsl(31, 100%, 50%);
  --red: hsl(347, 100%, 45.3%);
  --yellow: hsl(49, 100%, 50%);
  --white: hsl(240, 100%, 99%);
  --black: hsl(40, 8%, 15%);
}

:root {
  --font-header: "Roboto", sans-serif;
  --font-body: "Libre Baskerville", serif;
}

.card {
  padding: 1.25rem;
}

.footer {
  background-color: var(--darkGreen);
  width: 100%;
  height: 1.25rem;
}
.footer h1 {
  font-size: 1.25rem;
  color: var(--black);
}